import axios from "axios";
import { useEffect, useState } from "react";
import SingleContent from "../../components/singleContent/SingleContent";
import CustomPagination from "../../components/Pagination/CustomPagination";
import Genres from "../../components/Genres";
import useGenres from "../../hooks/useGenre"; 




const Series = () => {
    const [page, setPage] = useState(1);
  const [content, setContent] = useState([]);
  const [numOfPages, setNumOfPages] = useState();
  const [selectedGenres, setSelectedGenres] = useState([]);
  const [genres, setGenres] = useState([]);
  const genreforURL=useGenres(selectedGenres);

  const fetchMovies = async () => {
    const { data } = await axios.get(
      //`https://api.themoviedb.org/3/trending/movie/day?api_key=${process.env.REACT_APP_API_KEY}&page=${page}`
      `https://api.themoviedb.org/3/discover/tv?api_key=f1696e18bc893f4f42c97af5bc890904&page=${page}&with_genres=${genreforURL}`
    );
    //console.log(data);
    setContent(data.results);
    setNumOfPages(data.total_pages);
  };

  useEffect(() => {
    // async function apicall(){
    //     const res = await axios.get('https://api.themoviedb.org/3/trending/movie/day?api_key=f1696e18bc893f4f42c97af5bc890904');
    //     console.log(res.data);
    //     setContent(res);
    //apicall();
    fetchMovies();
  }, [page,genreforURL]);




    return (
        <div>
            <span className="pageTitle">TV Series</span>
            <Genres 
        type="tv"
        selectedGenres={selectedGenres}
        setSelectedGenres={setSelectedGenres}
        genres={genres}
        setGenres={setGenres}
        setPage={setPage}
      />
        <div className="trending">
        {content &&
          content.map((c) => (
            <SingleContent
              key={c.id}
              id={c.id}
              poster={c.poster_path}
              title={c.title || c.name}
              date={c.first_air_date || c.release_date}
              media_type="tv"
              vote_average={c.vote_average}
            />
          ))}
      </div>
      {numOfPages > 1 && (
        <CustomPagination setPage={setPage} numOfPages={numOfPages} />
      )}
        </div>
    )
}

export default Series